:root {
  --container-width: 600px;
  --font-family: "Montserrat", sans-serif;
  --font-size-small: .8rem;
  --font-size-medium: .9rem;
  --border-radius: .3rem;
  --transition-timing: .3s;
  --font-weight-bold: 600;
  --font-weight-bolder: 700;
  --body-bg: #1c1d20;
  --main-text-color: #c6e1f9;
  --loader-color: #2c83cf;
  --border-color: #627d96;
  --button-bg: #143e63cc;
  --button-hover-bg: #0b2c49;
  --button-text-color: #fff;
  --search-bg: #10192cb8;
  --error-text-color: #f19c9c;
  --error-bg: #2e0000;
  --placeholder-color: #ffffff61;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
}

body {
  font-family: var(--font-family);
  background: var(--body-bg);
  color: var(--main-text-color);
  line-height: 1.4;
}

.container {
  max-width: var(--container-width);
  grid-template-columns: 1fr;
  margin: 5rem auto;
  padding: 0 1rem;
  display: grid;
}

.title {
  justify-self: center;
  position: relative;
}

.title:after {
  content: "";
  aspect-ratio: 1;
  background-image: url("searcher.cca333cf.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 2rem;
  position: absolute;
  top: 50%;
  right: -2.5rem;
  transform: translateY(-50%);
}

.legend {
  display: none;
}

.finder-form {
  margin-block: 4rem 2rem;
}

.fieldset {
  max-width: 12rem;
  font-size: var(--font-size-medium);
  border: .1rem solid var(--border-color);
  border-radius: var(--border-radius);
  grid-template-columns: repeat(2, 1fr);
  margin-block-end: .9rem;
  display: grid;
}

.label {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  padding: .2rem .4rem;
  display: flex;
}

.label.checked {
  background: var(--border-color);
  font-weight: var(--font-weight-bold);
  transition: var(--transition-timing) ease-in-out;
}

.label:not(:last-of-type) {
  border-right: .15rem solid var(--border-color);
}

.input[type="radio"] {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.search-box {
  background: var(--search-bg);
  border-radius: var(--border-radius);
  width: 100%;
  transition: var(--transition-timing);
  justify-content: flex-end;
  align-items: center;
  display: flex;
  position: relative;
}

.search-box:before {
  content: "";
  aspect-ratio: 1;
  background-image: url("search.10b4f692.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 1.2rem;
  margin-inline: 1.7rem .4rem;
}

.error-message {
  font-size: var(--font-size-small);
  color: var(--error-text-color);
  background: var(--error-bg);
  border-radius: var(--border-radius);
  z-index: 1;
  opacity: 0;
  transition: opacity var(--transition-timing) ease-in-out;
  align-items: center;
  gap: .3rem;
  padding: .4rem .7rem;
  display: inline-flex;
  position: relative;
}

.error-message.appear {
  opacity: 1;
}

.error-message:before {
  content: "";
  aspect-ratio: 1;
  width: 1rem;
  transition: var(--transition-timing) ease-in-out;
  background-image: url("warning.41c34333.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.error-message:after {
  content: "";
  aspect-ratio: 1;
  background: inherit;
  z-index: -1;
  width: .8rem;
  transition: var(--transition-timing) ease-in-out;
  position: absolute;
  top: -.4rem;
  left: .8rem;
  transform: rotate(45deg);
}

.search {
  width: 100%;
  color: var(--button-text-color);
  background: none;
  border: none;
  flex: 1;
  padding-block: 1rem;
  font-family: inherit;
}

.search::placeholder {
  color: var(--placeholder-color);
}

.search:focus {
  outline: none;
}

.search:-webkit-autofill {
  transition: background-color 5000s ease-in-out;
  -webkit-box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  -webkit-text-fill-color: var(--button-text-color) !important;
  caret-color: var(--button-text-color) !important;
}

.search:-webkit-autofill:hover {
  transition: background-color 5000s ease-in-out;
  -webkit-box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  -webkit-text-fill-color: var(--button-text-color) !important;
  caret-color: var(--button-text-color) !important;
}

.search:-webkit-autofill:focus {
  transition: background-color 5000s ease-in-out;
  -webkit-box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  -webkit-text-fill-color: var(--button-text-color) !important;
  caret-color: var(--button-text-color) !important;
}

.search:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out;
  -webkit-box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  box-shadow: 0 0 0px 1000px var(--search-bg) inset !important;
  -webkit-text-fill-color: var(--button-text-color) !important;
  caret-color: var(--button-text-color) !important;
}

.button {
  border-radius: var(--border-radius);
  background: var(--button-bg);
  color: var(--button-text-color);
  font-weight: var(--font-weight-bold);
  cursor: pointer;
  transition: background var(--transition-timing) ease-in-out;
  border: none;
  margin-inline-end: .8rem;
  padding: .4rem 1.2rem;
  font-family: inherit;
}

.button:hover {
  background: var(--button-hover-bg);
}

.repos-box {
  border-left: .2rem solid var(--button-bg);
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  gap: 3rem 1rem;
  display: grid;
}

.repo {
  text-align: center;
  color: var(--main-text-color);
  text-decoration: none;
}

.text {
  font-size: var(--font-size-medium);
}

.image {
  border-radius: 50%;
  width: 6rem;
  max-width: 100%;
}

.loader.appear {
  aspect-ratio: 1;
  background: var(--loader-color);
  --_m: conic-gradient(#0000 10%, #000), linear-gradient(#000 0 0) content-box;
  width: 50px;
  -webkit-mask: var(--_m);
  mask: var(--_m);
  -webkit-mask-composite: source-out;
  border-radius: 50%;
  justify-self: center;
  padding: 8px;
  animation: 1s linear infinite loader;
  mask-composite: subtract;
}

@keyframes loader {
  to {
    transform: rotate(1turn);
  }
}
